export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			trailerList: [],
			playVidio: null,
		}
	},
	methods: {
		stop() { 
			console.log('222')
			this.playVidio.pause()
		},
		getVideoId(index) {
			return `myVideo${index}`
		},
		play(index) {
			let a = `myVideo${index}`
			let newVideo = uni.createVideoContext(a);
			console.log(newVideo)
			if (this.playVidio && this.playVidio.id !== newVideo.id) {
			
				newVideo.play()
				this.playVidio.pause()
			}
			this.playVidio = newVideo
			
		},
		getTrailer() {
			uni.showLoading()
			uni.request({
				url: "https://www.imovietrailer.com/superhero/index/movie/hot?qq=2684425594&type=trailer",
				method: 'POST',
				data: {
					qq: 2684425594,
					type: 'superhero'
				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {

					if (res.statusCode === 200) {
						this.trailerList = res.data.data
						
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		}
	},
	beforeMount() {
		this.getTrailer()
	},
